import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import foto1 from "../../images/fresh-papaya-and-citrus-fruits-delicious-composition-4113834.jpg"

const Ubuntu = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Relación entre deficiencia de vitamina D y mortalidad de COVID-19"
                meta="Los investigadores observaron que los pacientes de países con altas tasas de mortalidad por COVID-19, como Italia, España y el Reino Unido, tenían niveles más bajos de vitamina D en comparación con los pacientes de países que no estaban tan gravemente afectados."
                description="Los investigadores observaron que los pacientes de países con altas tasas de mortalidad por COVID-19, como Italia, España y el Reino Unido, tenían niveles más bajos de vitamina D en comparación con los pacientes de países que no estaban tan gravemente afectados."
                lang="es"
            />
            <h1>Los investigadores han descubierto una fuerte correlación entre la deficiencia severa de vitamina D y las tasas de mortalidad de COVID-19</h1>
            <hr/>
            <p class="introductionText">Despu&eacute;s de estudiar los datos mundiales de la nueva pandemia de
                coronavirus (COVID-19), los
                investigadores han descubierto una fuerte correlaci&oacute;n entre la deficiencia grave de vitamina
                D y las tasas de mortalidad.</p>
            <hr/>
            <p></p>
            <div>
                <p></p>
                <p>Dirigido por la Universidad Northwestern, el equipo de investigaci&oacute;n realiz&oacute; un
                    an&aacute;lisis estad&iacute;stico de los datos de hospitales y cl&iacute;nicas de China, Francia,
                    Alemania, Italia, Ir&aacute;n, Corea del Sur, Espa&ntilde;a, Suiza, el Reino Unido (UK) y los
                    Estados Unidos.</p>
                <p></p>
                <p><b>Los investigadores observaron que los pacientes de pa&iacute;ses con altas tasas de mortalidad por
                    COVID-19, como Italia, Espa&ntilde;a y el Reino Unido, ten&iacute;an niveles m&aacute;s bajos de

                    <a className="link" href="/vitaminaC"> vitamina D</a> en comparaci&oacute;n con los pacientes de pa&iacute;ses que no estaban tan gravemente
                    afectados.</b></p>
                <p></p>
                <div style={{textAlign:`center`,margin:'50px'}}>
                    <img src={foto1} style={{width:`50vh`}} alt="papaya"></img>
                </div>
                <p>Esto no significa que todos, especialmente los que no tienen una deficiencia conocida, necesiten
                    empezar a acumular suplementos, advierten los investigadores.</p>
                <p></p>
                <p>&quot;Aunque creo que es importante que la gente sepa que la deficiencia de vitamina D podr&iacute;a
                    tener que ver con la mortalidad, no necesitamos imponer la vitamina D a todo el mundo&quot;, dijo
                    Vadim Backman de Northwestern, quien dirigi&oacute; la investigaci&oacute;n. &quot;Esto necesita
                    m&aacute;s estudio, y espero que nuestro trabajo estimule el inter&eacute;s en esta &aacute;rea. Los
                    datos tambi&eacute;n pueden iluminar el mecanismo de la mortalidad, que, si se demuestra,
                    podr&iacute;a conducir a nuevos objetivos terap&eacute;uticos&quot;.</p>
                <p></p>
                <p>La investigaci&oacute;n est&aacute; disponible en medRxiv, un servidor de preimpresi&oacute;n para
                    ciencias de la salud.</p>
                <p></p>
                <p>Backman es el Profesor Walter Dill Scott de Ingenier&iacute;a Biom&eacute;dica en la Escuela de
                    Ingenier&iacute;a McCormick de Northwestern. Ali Daneshkhah, investigador asociado postdoctoral en
                    el laboratorio de Backman, es el primer autor del art&iacute;culo.</p>
                <p></p>
                <p>Backman y su equipo se inspiraron en el examen de los niveles de vitamina D despu&eacute;s de notar
                    diferencias inexplicables en las tasas de mortalidad de COVID-19 de un pa&iacute;s a otro. Algunas
                    personas formularon la hip&oacute;tesis de que las diferencias en la calidad de la atenci&oacute;n
                    sanitaria, la distribuci&oacute;n de edades en la poblaci&oacute;n, las tasas de pruebas o las
                    diferentes cepas del coronavirus podr&iacute;an ser las responsables. Pero Backman segu&iacute;a
                    siendo esc&eacute;ptico.</p>
                <p></p>
                <p>&quot;Ninguno de estos factores parece desempe&ntilde;ar un papel significativo&quot;, dijo
                    Backman. &quot;El sistema de salud en el norte de Italia es uno de los mejores del mundo. Las
                    diferencias en la mortalidad existen incluso si se mira a trav&eacute;s del mismo grupo de edad. Y,
                    aunque las restricciones de las pruebas var&iacute;an, las disparidades en la mortalidad siguen
                    existiendo incluso cuando miramos a pa&iacute;ses o poblaciones en los que se aplican tasas de
                    pruebas similares.</p>
                <p></p>
                <p>&quot;En cambio, vimos una correlaci&oacute;n significativa con la deficiencia de vitamina D&quot;,
                    dijo.</p>
                <p></p>
                <p><b>Al analizar los datos de pacientes disponibles p&uacute;blicamente en todo el mundo, Backman y su
                    equipo descubrieron una fuerte correlaci&oacute;n entre los niveles de vitamina D y la tormenta de
                    citoquinas -una condici&oacute;n hiperinflamatoria causada por un sistema inmunol&oacute;gico
                    hiperactivo- as&iacute; como una correlaci&oacute;n entre la deficiencia de vitamina D y la
                    mortalidad.</b></p>
                <p></p>
                <p>&quot;La tormenta de citoquinas puede da&ntilde;ar gravemente los pulmones y provocar el
                    s&iacute;ndrome de dificultad respiratoria aguda y la muerte de los pacientes&quot;, dijo
                    Daneshkhah. &quot;Esto es lo que parece matar a la mayor&iacute;a de los pacientes de COVID-19, no
                    la destrucci&oacute;n de los pulmones por el propio virus. Son las complicaciones del fuego mal
                    dirigido del sistema inmunol&oacute;gico&quot;.</p>
                <p></p>
                <p>Aqu&iacute; es exactamente donde Backman cree que la vitamina D juega un papel importante. <b>La
                    vitamina D no s&oacute;lo mejora nuestro sistema inmunol&oacute;gico innato, sino que tambi&eacute;n
                    evita que nuestro sistema inmunol&oacute;gico se vuelva peligrosamente hiperactivo</b>. Esto significa
                    que tener niveles saludables de vitamina D podr&iacute;a proteger a los pacientes contra
                    complicaciones severas, incluyendo la muerte, de COVID-19.</p>
                <p></p>
                <p>&quot;Nuestro an&aacute;lisis muestra que podr&iacute;a ser tan alto como reducir la tasa de
                    mortalidad a la mitad&quot;, dijo Backman. &quot;No evitar&aacute; que un paciente contraiga el
                    virus, pero puede reducir las complicaciones y evitar la muerte de los infectados&quot;.</p>
                <p></p>
                <p>Backman dijo que esta correlaci&oacute;n podr&iacute;a ayudar a explicar los muchos misterios que
                    rodean a COVID-19, como por ejemplo por qu&eacute; los ni&ntilde;os son menos propensos a morir. Los
                    ni&ntilde;os a&uacute;n no tienen un sistema inmunol&oacute;gico adquirido completamente
                    desarrollado, que es la segunda l&iacute;nea de defensa del sistema inmunol&oacute;gico y es
                    m&aacute;s probable que reaccione de forma exagerada.</p>
                <p></p>
                <p>&quot;Los ni&ntilde;os dependen principalmente de su sistema inmunol&oacute;gico innato&quot;, dijo
                    Backman. &quot;Esto puede explicar por qu&eacute; su tasa de mortalidad es menor&quot;.</p>
                <p></p>
                <p>Backman es cuidadoso en se&ntilde;alar que la gente no debe tomar dosis excesivas de vitamina D, lo
                    que podr&iacute;a venir con efectos secundarios negativos. Dijo que el tema necesita mucha
                    m&aacute;s investigaci&oacute;n para saber c&oacute;mo la vitamina D podr&iacute;a ser utilizada
                    m&aacute;s eficazmente para protegerse contra las complicaciones de COVID-19.</p>
                <p></p>
                <p>&quot;Es dif&iacute;cil decir qu&eacute; dosis es la m&aacute;s beneficiosa para COVID-19&quot;, dijo
                    Backman. &quot;Sin embargo, est&aacute; claro que la deficiencia de vitamina D es perjudicial, y se
                    puede tratar f&aacute;cilmente con los suplementos apropiados. Esto podr&iacute;a ser otra clave
                    para ayudar a proteger a las poblaciones vulnerables, como los afroamericanos y los pacientes
                    ancianos, que tienen una prevalencia de deficiencia de vitamina D&quot;.</p>
                <p></p>
                <p>Backman es el director del Centro de Gen&oacute;mica F&iacute;sica e Ingenier&iacute;a de
                    Northwestern y el director asociado de Tecnolog&iacute;a e Infraestructura de Investigaci&oacute;n
                    del Centro Integral de C&aacute;ncer Robert H. Lurie de la Universidad de Northwestern.</p>
                <p></p>
            </div>
            <hr/>
            <p><b>Fuente: </b><a class="link" rel="nofollow"
                                 href="https://www.medrxiv.org/content/10.1101/2020.04.08.20058578v4">https://www.medrxiv.org/content/10.1101/2020.04.08.20058578v4</a>
            </p>
        </Layout>
    )
}

export default Ubuntu
